<script>
import Card from "./Card.svelte"
import ClassFeature from "./ClassFeature.svelte"

export let doc

let open = true

function toggleOpen() {
  open = !open
}
</script>

<div class="segment {doc.type}">
  <h3 class="collapsible {open ? 'open' : 'closed'}"  on:click={toggleOpen}>{doc.name}</h3>
  <div class="card-collection">
    {#each doc.features as feature}
      <ClassFeature bind:doc={feature} bind:open={open}/>
    {/each}
  </div>
</div>

