<script>
import Card from "./Card.svelte"
import Action from "./Action.svelte"

export let doc
let color
if (doc.type === "Arcana") {
  color = "#040"
} else if (doc.type === "Discipline") {
  color = "#400"
}

let open = true

function toggleOpen() {
  open = !open
}
</script>

<div class="segment {doc.type}">
  <h3 class="collapsible {open ? 'open' : 'closed'}"  on:click={toggleOpen}>{doc.name} ({doc.type})</h3>
  <div class="card-collection">
    {#each doc.actions as action}
      <Action bind:doc={action} bind:open={open}/>
    {/each}
  </div>
</div>

<style>

</style>
